import React, { useState } from "react";
import Button from "./Button";

import { ArrowLeftCircleIcon } from "@heroicons/react/24/outline";
import usestepStore from "../store/step";
import useRouteStore from "../store/route";
import BookSkelton from "./BookSkelton";
import mini from "../assets/ERickshaw.webp";
import tataace from "../assets/tataace.webp";
import bolero from "../assets/bolero.webp";
import { toast } from "react-toastify";
const Book = () => {
  const data = [
    {
      id: "tataace",
      title: "Chhota Hathi",
      morningRate: 9,
      nightRate: 12,
      image: tataace,
      capacity: 2000,
    },
    {
      id: "bolero",
      title: "Pick-up",
      morningRate: 9,
      nightRate: 12,
      image: bolero,
      capacity: 3000,
    },
  ];
  const { current, setStep } = usestepStore((state) => ({
    current: state.current,
    setStep: state.setStep,
  }));

  const [selected, setSelected] = useState(null);
  const { route } = useRouteStore((state) => ({
    route: state.route,
  }));

  const { setDirection, setMap, loading } = useRouteStore((state) => ({
    setDirection: state.setDirection,
    loading: state.loading,
  }));

  const calculatePrice = (data) => {
    let rate =
      new Date(route.pickupTime).getHours() > 20
        ? data.nightRate
        : data.morningRate;

    let discountedPrice =
      Math.ceil((rate * route.distanceValue) / 1000) + route.toll;
    let price = route.toll + discountedPrice + discountedPrice * 0.3;

    if ("tataace" === data.id) {
      if (route.distanceValue < 5000) {
        price = 500
      }else if(route.distanceValue > 5000 && route.distanceValue < 30000){
        price = 500 + (route.distanceValue-5000)*60/1000;
      }else{
        price = 500 + (route.distanceValue-5000)*60/1000 + (route.distanceValue-30000)*50/1000
      }
    }

    if(data.id === "bolero"){
      if (route.distanceValue < 4000) {
        price = 500
      }else if(route.distanceValue > 4000 && route.distanceValue < 8000){
        price = 800
      }else if(route.distanceValue > 8000 && route.distanceValue < 24000){
        price = 1300 + (route.distanceValue-8000)*90/1000;
      }else{
        price = 1300 + (route.distanceValue-8000)*90/1000 + (route.distanceValue-24000)*75/1000
      }
    }

    discountedPrice = Math.ceil(price*1.05);
    price= Math.ceil(discountedPrice*1.03);

    // if(route.distanceValue > 20000){
    //   price = route.toll + discountedPrice + discountedPrice * 0.5;
    // }
    return { discountedPrice, price };
  };

  const onBookClick = () => {
    toast.success(`${data[selected].title} booked successfully`);
    setStep("Form");
    setDirection(undefined);
  };
  if (current === "Book") {
    return (
      <div className="absolute top-24 left-7 bg-white rounded-lg p-5">
        {loading ? (
          <>
            <BookSkelton />
            <BookSkelton />
            <BookSkelton />
            <BookSkelton />
          </>
        ) : (
          <div className="  rounded relative">
            <div className="flex ">
              <ArrowLeftCircleIcon
                onClick={() => {
                  setStep("Form");
                  setDirection(undefined);
                }}
                className="w-7 mb-2 cursor-pointer "
              />
              <h4 className="mx-auto text-lg font-bold text-gray-700">
                Select Ride
              </h4>
            </div>
            <div className="flex justify-between mb-2 text-gray-800">
              <div>
                Estimated Time :
                <span className="font-bold"> {route.duration}</span>
              </div>
              <div>
                Distance : <span className="font-bold">{route.distance}</span>
              </div>
            </div>
            {data.map((data, index) => (
              <div
                key={index}
                className={`flex items-center cursor-pointer p-3 border hover:border-gray-600  mb-3 rounded-md shadow ${
                  selected === index && "border-gray-600 "
                }`}
                onClick={() => {
                  setSelected(index);
                }}
              >
                <img className="w-20" src={data.image} alt="" />
                <div>
                  <h3 className="text-lg font-bold text-gray-600">
                    {data.title}
                  </h3>
                  <div className="flex items-center ">
                    <span>{data.capacity+ " "}</span>
                     Kg
                  </div>
                </div>
                <div className="ml-24 flex items-center flex-col ">
                  <div className=" font-bold flex ">
                    {/* <InformationCircleIcon className="w-4" /> */}
                    &nbsp; &#8377; {calculatePrice(data).discountedPrice}
                  </div>
                  <p className=" text-gray-400 line-through text-sm">
                    &#8377; {calculatePrice(data).price}
                  </p>
                </div>
              </div>
            ))}
            <div>
              <Button
                onClick={onBookClick}
                className="w-full mx-3"
                disabled={selected === null}
              >
                Book {selected !== null ? data[selected].title : "Any"}
              </Button>
            </div>
          </div>
        )}
      </div>
    );
  } else {
    return null;
  }
};

export default Book;
